.supervisor {
	padding : 8px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 12px 0;
	}
}

.supervisor__grid {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}
}

.supervisor__left {
	background-color    : $light-blue;
	background-image    : url("../images/icon/bg-wrap-tablet.jpg");
	background-position : center center;
	background-repeat   : no-repeat;
	background-size     : cover;
	display             : flex;
	flex-direction      : column;
	grid-column         : 4 span;
	justify-content     : flex-end;
	padding-top         : 22px;
	text-align          : center;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		background-image : url("../images/icon/bg-wrap-desktop.jpg");
		grid-column      : 6 span;
		padding-top      : 54px;
	}

	img {
		@media screen and (min-width : $break-table) {
			width : 348px;
		}

		@media screen and (min-width : $break-desktop) {
			width : 474px;
		}
	}
}

.supervisor__right {
	grid-column : 4 span;
	padding     : 40px 40px;

	@media screen and (min-width : $break-table) {

		padding : 64px 64px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 6 span;
	}
}

.supervisor__spoiler {
	background-color : $white;
	border-radius    : 64px;
	color            : $blue;
	display          : inline-block;
	font-size        : 16px;
	font-style       : normal;
	font-weight      : 500;
	line-height      : 24px;
	margin-bottom    : 24px;
	padding          : 12px 24px;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 16px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.supervisor__title {
	color         : #262526;
	font-size     : 32px;
	font-weight   : 600;
	line-height   : 40px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		font-size     : 48px;
		line-height   : 56px;
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 64px;
		line-height   : 72px;
		margin-bottom : 48px;
	}
}

.supervisor__text {
	color         : #0B0B0B;
	font-size     : 14px;
	font-style    : normal;
	font-weight   : 500;
	line-height   : 20px;
	margin-bottom : 32px;
	opacity       : 0.8;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
