.reporting {
	padding : 8px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 12px 0;
	}
}

.reporting__wrapper {
	background-color : $light-blue;
	border-radius    : 30px;
	padding          : 40px 40px;

	@media screen and (min-width : $break-table) {
		padding : 64px 64px;
	}

	@media screen and (min-width : $break-desktop) {

	}

}

.reporting__title {
	margin-bottom : 40px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 64px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 585px;
	}
}

.reporting__grid {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}
}

.reporting__link {
	background-color : $white;
	border-radius    : 30px;
	display          : flex;
	flex-direction   : column;
	grid-column      : 4 span;
	justify-content  : flex-end;
	min-height       : 156px;
	padding          : 24px 24px;
	position         : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		min-height : 228px;
		padding    : 32px 32px;
	}

	&:before {
		background-image  : url("../images/icon/pdf.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		height            : 36px;
		position          : absolute;
		right             : 24px;
		top               : 24px;
		width             : 59px;
		z-index           : 1;

		@media screen and (min-width : $break-desktop) {
			right : 32px;
			top   : 32px;
		}
	}

	&:hover {
		svg {
			transform : rotate(45deg);

			circle {
				fill       : $blue;
				transition : fill .3s ease;
			}

			path {
				fill       : $white;
				transition : fill .3s ease;
			}
		}

		.reporting__wrap {
			span {
				color : $blue;
			}
		}
	}

	svg {
		transition : transform .3s ease;


		circle {
			fill       : #F2F8FF;
			transition : fill .3s ease;
		}

		path {
			fill       : $blue;
			transition : fill .3s ease;
		}
	}
}

.reporting__wrap {
	align-items     : flex-end;
	display         : flex;
	justify-content : space-between;

	span {
		color       : #262526;
		font-size   : 20px;
		font-weight : 800;
		line-height : 28px;
		max-width   : 165px;
		transition  : color .3s ease;

		@media screen and (min-width : $break-table) {
			font-size   : 24px;
			line-height : 32px;
			max-width   : 165px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}
