.partners {
	padding : 8px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 12px 0;
	}
}

.partners__grid {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}
}

.partners__left {
	background-color : $light-blue;
	border-radius    : 30px;
	grid-column      : 4 span;
	padding          : 40px 40px;

	@media screen and (min-width : $break-table) {
		padding : 64px 64px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 6 span;
	}

	.b-btn--blue {
		min-width : 248px;
		padding   : 12px 37px;

		@media screen and (min-width : $break-table) {
			min-width : auto;
			padding   : 20px 32px;
		}

		@media screen and (min-width : $break-desktop) {
			padding : 20px 37px;
		}

		&:hover {
			&:before {
				border-radius : 40px;
			}
		}

		&:before {
			border-radius : 40px;
		}
	}
}

.partners__right {
	background-color    : $light-blue;
	background-image    : url("../images/icon/bg-wrap-tablet.jpg");
	background-position : center center;
	background-repeat   : no-repeat;
	background-size     : cover;
	border-radius       : 30px;
	grid-column         : 4 span;
	grid-row            : 1/1;
	text-align          : center;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		background-image : url("../images/icon/bg-wrap-desktop.jpg");
		grid-column      : 6 span;
		grid-row         : auto;
	}
}

.partners__title {
	color         : #262526;
	font-size     : 18px;
	font-weight   : 600;
	line-height   : 24px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		font-size     : 32px;
		line-height   : 40px;
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.partners__text {
	color         : #0B0B0B;
	font-size     : 14px;
	font-weight   : 500;
	line-height   : 20px;
	margin-bottom : 32px;
	opacity       : 0.8;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 64px;
	}
}
