// -----------------------------------------------
//  Button config
// -----------------------------------------------

$btn-font-weight           : 500;
$btn-line-height           : 24px;
$btn-border-width          : 1px;
$btn-padding               : 8px 20px;
$btn-font-size             : 16px;
$btn-border-radius         : 40px;
$btn-transition            : background .3s ease, color .3s ease;

// OUTLINE

// SIZE
// Big
$btn-padding-big           : 14px 30px;
$btn-font-size-big         : 16px;
// Small
$btn-padding-small         : 8px 30px;
$btn-font-size-small       : 13px;

// COLOR
// White
$btn-white-bg              : #DCDCDE;
$btn-white-color           : #0B081B;
$btn-white-bg-hover        : #BDBDBF;
$btn-white-color-hover     : #0B081B;


// Main
$btn-main-bg               : #FFF;
$btn-main-color            : #0D50C3;
$btn-main-color-outline    : #0D50C3;
$btn-main-bg-hover         : linear-gradient(0deg, #041B41 0%, #041B41 100%), #041B41;
$btn-main-color-hover      : #FFF;


// Blue
$btn-blue-bg               : linear-gradient(134deg, #0D50C3 0%, #3C79E2 100%), #0D50C3;
$btn-blue-color            : #FFF;
$btn-blue-color-outline    : $btn-blue-bg;
$btn-blue-bg-hover         : darken(#18A3FA, 10%);
$btn-blue-color-hover      : #FFF;


// LOADING
$btn-loading-color         : #FFF;
$btn-loading-color-outline : #000;
// SPACING
$btn-spacing-x             : 5px;
$btn-spacing-y             : 5px;

// -----------------------------------------------
//  Button style
// -----------------------------------------------

.b-btn {
	align-items        : center;
	border             : none;
	border-radius      : $btn-border-radius;
	-moz-box-sizing    : border-box;
	-webkit-box-sizing : border-box;
	box-sizing         : border-box;
	display            : inline-flex;
	flex-flow          : row nowrap;
	font-size          : $btn-font-size;
	font-weight        : $btn-font-weight;
	justify-content    : center;
	line-height        : $btn-line-height;
	padding            : $btn-padding;
	position           : relative;
	text-align         : center;
	text-decoration    : none;
	transition         : $btn-transition;
	user-select        : none;
	vertical-align     : middle;
	white-space        : nowrap;

	&:hover,
	&:focus-within,
	&.focus {
		outline : 0;
	}

	&.disabled,
	&:disabled {
		background : inherit;
		box-shadow : none;
		cursor     : default;
		opacity    : .45;
	}

	&:active,
	&.active {

	}

	[class^="b-icon-"],
	[class*=" b-icon-"] {
		margin-right   : 10px;
		vertical-align : middle;
	}

	// SIZE
	// Big
	&--big {
		font-size : $btn-font-size-big;
		padding   : $btn-padding-big;
	}

	&--small {
		font-size : $btn-font-size-small;
		padding   : $btn-padding-small;
	}

	// COLOR

	// main
	&--main {
		background    : $btn-main-bg;
		border-radius : 24px;
		color         : $btn-main-color;
		padding       : 20px 32px;
		position      : relative;
		transition    : background .3s ease;
		z-index       : 1;

		span {
			font-size   : 24px;
			font-weight : 500;
			line-height : 32px;
			position    : relative;
			transition  : color .3s ease;
			z-index     : 3;
		}

		&:before {
			background    : linear-gradient(0deg, #041B41 0%, #041B41 100%), #041B41;
			border-radius : 24px;
			bottom        : 0;
			content       : "";
			left          : 0;
			opacity       : 0;
			position      : absolute;
			right         : 0;
			top           : 0;
			transition    : opacity .3s ease;
			z-index       : -1;
		}

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			color : $white;

			&:before {
				opacity : 1;
				z-index : 2;
			}
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-main-bg;
				color            : $btn-main-color;
			}
		}
	}


	// Blue
	&--blue {
		background : $btn-blue-bg;
		color      : $btn-blue-color;
		position   : relative;
		transition : background .3s ease;
		z-index    : 1;

		span {
			position   : relative;
			transition : color .3s ease;
			z-index    : 3;
		}

		&:before {
			background    : linear-gradient(0deg, #041B41 0%, #041B41 100%), #041B41;
			border-radius : 24px;
			bottom        : 0;
			content       : "";
			left          : 0;
			opacity       : 0;
			position      : absolute;
			right         : 0;
			top           : 0;
			transition    : opacity .3s ease;
			z-index       : -1;
		}

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			color : $white;

			&:before {
				opacity : 1;
				z-index : 2;
			}
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-blue-bg;
				color            : $btn-blue-color;
			}
		}
	}


	// FULL-SIZE
	&--full-size {
		display : block;
		width   : 100%;

		+ .full-size {
			margin-right : $btn-spacing-y;
		}
	}

	// SPACING
	& + .b-btn {
		margin-left : $btn-spacing-x;
	}
}

.b-btn-grouping {
	display : inline-block;

	.b-btn {
		border-radius : 0;
		float         : left;
		margin-right  : 0;

		&:first-child {
			border-radius : $btn-border-radius 0 0 $btn-border-radius;
		}

		&:last-child {
			border-radius : 0 $btn-border-radius $btn-border-radius 0;
		}

		&.outline {
			margin-left : -1px;
			z-index     : 1;

			&:hover {
				z-index : 2;
			}

			&.active {
				z-index : 3;
			}

			&:first-child {
				margin-left : 0;
			}
		}
	}
}

@keyframes loadingSpin {
	0% {
		transform : rotate(0deg);
	}
	100% {
		transform : rotate(360deg);
	}
}

// Future-proof disabling of clicks on `<a>` elements
a.b-btn.disabled,
fieldset[disabled] a.b-btn {
	pointer-events : none;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
	&.full-size {
		width : 100%;
	}
}
