.hero {
	padding-bottom : 8px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 12px;
	}
}

.hero__top {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 24px;
	}
}

.hero__left {
	background    : linear-gradient(134deg, #0D50C3 0%, #3C79E2 100%);
	border-radius : 30px;
	grid-column   : 4 span;
	padding       : 40px 40px;
	position      : relative;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
		padding     : 64px 64px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 6 span;
	}

	&:before {
		background-image  : url("../images/home/hero/hero-bg.png");
		background-repeat : no-repeat;
		background-size   : cover;
		bottom            : 0;
		content           : "";
		display           : none;
		height            : 290px;
		position          : absolute;
		right             : 0;
		width             : 358px;

		@media screen and (min-width : $break-table) {
			display : block;
		}
	}

	.b-btn--main {
		width : 100%;

		span {
			font-size   : 16px;
			line-height : 24px;
		}

		@media screen and (min-width : $break-table) {
			width : auto;

			span {
				font-size   : 24px;
				line-height : 32px;
			}
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.hero__title {
	color         : $white;
	font-size     : 32px;
	font-weight   : 600;
	line-height   : 40px;
	margin-bottom : 8px;
	max-width     : 248px;

	@media screen and (min-width : $break-table) {
		font-size     : 56px;
		line-height   : 64px;
		margin-bottom : 18px;
		max-width     : 592px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 80px;
		line-height   : 88px;
		margin-bottom : 24px;
	}
}

.hero__text {
	color         : $white;
	font-size     : 16px;
	font-style    : normal;
	font-weight   : 500;
	line-height   : 24px;
	margin-bottom : 32px;
	max-width     : 187px;

	@media screen and (min-width : $break-table) {
		font-size     : 24px;
		line-height   : 32px;
		margin-bottom : 48px;
		max-width     : 270px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.hero__right {
	background-color    : $light-blue;
	background-image    : url("../images/icon/bg-wrap-tablet.jpg");
	background-position : center center;
	background-repeat   : no-repeat;
	background-size     : cover;
	border-radius       : 30px;
	display             : flex;
	flex-direction      : column;
	grid-column         : 4 span;
	justify-content     : flex-end;
	text-align          : center;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
	}

	@media screen and (min-width : $break-desktop) {
		background-image : url("../images/icon/bg-wrap-desktop.jpg");
		grid-column      : 6 span;
	}

	img {
		@media screen and (min-width : $break-table) {
			width : 367px;
		}

		@media screen and (min-width : $break-desktop) {
			width : 648px;
		}
	}
}

.hero__item {
	background-color : $light-blue;
	border-radius    : 30px;
	display          : flex;
	flex-direction   : column;
	grid-column      : 2 span;
	height           : 100%;
	padding          : 24px 24px;

	@media screen and (min-width : $break-table) {
		grid-column : 4 span;
	}

	@media screen and (min-width : $break-desktop) {
		align-items     : center;
		flex-direction  : row;
		justify-content : space-between;
	}

	.hero__text {
		color         : #393A3C;
		font-size     : 12px;
		font-style    : normal;
		font-weight   : 500;
		line-height   : 18px;
		margin-bottom : 32px;

		@media screen and (min-width : $break-table) {
			font-size   : 14px;
			line-height : 20px;
			max-width   : 180px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 0;
		}
	}

	&--blue {
		align-items     : center;
		background      : linear-gradient(134deg, #0D50C3 0%, #3C79E2 100%);
		display         : flex;
		flex-direction  : row;
		grid-column     : 4 span;
		justify-content : space-between;

		@media screen and (min-width : $break-table) {
			align-items    : flex-start;
			flex-direction : column;
		}

		@media screen and (min-width : $break-desktop) {
			align-items    : center;
			flex-direction : row;
		}

		.hero__price {
			color       : $white;
			font-size   : 32px;
			line-height : 40px;
		}

		.hero__text {
			color         : $white;
			margin-bottom : 0;

			@media screen and (min-width : $break-table) {
				margin-bottom : 32px;
			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 0;
			}
		}
	}
}

.hero__price {
	color       : #262526;
	font-size   : 24px;
	font-weight : 600;
	line-height : 32px;
	margin-top  : auto;

	@media screen and (min-width : $break-table) {
		font-size   : 32px;
		line-height : 40px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
