.header {
	margin-bottom : 24px;
	margin-top    : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.header__wrapper {
	align-items      : center;
	background-color : $light-blue;
	border-radius    : 30px;
	display          : flex;
	justify-content  : space-between;
	padding          : 20px 16px;

	@media screen and (min-width : $break-table) {
		padding : 24px 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.header__logo {
	height : 31px;
	width  : 84px;

	@media screen and (min-width : $break-table) {
		height : 40px;
		width  : 106px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.header__panel {
	align-items : center;
	display     : flex;

	@media screen and (min-width : $break-desktop) {
		margin-left : 14px;
	}

	@media screen and (min-width : 1920px) {
		margin-left : 54px;
	}

	.b-btn--blue {
		display     : none;
		margin-left : 24px;

		@media screen and (min-width : $break-table) {
			display : inline-flex;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.nav {
	display     : none;
	margin-left : 40px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display : block;
	}


	&__list {
		align-items : center;
		display     : flex;
	}

	&__item {
		& + & {
			margin-left : 24px;
		}
	}

	&__link {
		color       : $blue;
		font-size   : 14px;
		font-weight : 500;
		line-height : 20px;
		position    : relative;

		&:hover {
			&:before {
				width : 100%;
			}
		}

		&:before {
			background-color : $blue;
			bottom           : -2px;
			content          : "";
			height           : 1px;
			left             : 0;
			position         : absolute;
			transition       : width .3s ease;
			width            : 0;
		}
	}
}

.socials {
	align-items : center;
	display     : flex;


	&__item {
		height : 40px;
		width  : 40px;

		& + & {
			margin-left : 8px;
		}
	}

	&__link {
		display : inline-block;
		height  : 40px;
		width   : 40px;

		svg {
			pointer-events : none;


			rect {
				fill       : $white;
				transition : fill .3s ease;
			}

			path {
				stroke     : $blue;
				transition : stroke .3s ease;
			}
		}

		&:hover {
			svg {

				rect {
					fill : $blue;
				}

				path {
					stroke : $white;
				}
			}
		}
	}
}
