.about {
	padding : 8px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 12px 0;
	}
}

.about__grid {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}
}

.about__left {
	background  : $bg-blue;
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 6 span;
	}
}

.about__title {
	color         : $white;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 50px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.about__text {
	color       : $white;
	font-size   : 14px;
	font-weight : 500;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.about__video {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 6 span;
	}
}

.about__wrapper {
	margin-bottom : 16px;
	position      : relative;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}
}

.video {
	border-radius : 30px;


	&__poster {
		img {
			border-radius : 30px;
			height        : 100%;
			object-fit    : cover;
			width         : 100%;
		}
	}

	&__btn {
		cursor    : pointer;
		height    : 64px;
		left      : 50%;
		position  : absolute;
		top       : 50%;
		transform : translate(-50%, -50%);
		width     : 64px;
		z-index   : 1;

		@media screen and (min-width : $break-table) {
			height : 120px;
			width  : 120px;
		}

		@media screen and (min-width : $break-desktop) {

		}

		svg {
			height : 64px;
			width  : 64px;
			@media screen and (min-width : $break-table) {
				height : 120px;
				width  : 120px;
			}
		}
	}
}

.about__wrap {
	background-color : $light-blue;
	border-radius    : 30px;
	color            : #0B0B0B;
	font-size        : 14px;
	font-weight      : 500;
	line-height      : 20px;
	padding          : 20px 0;
	text-align       : center;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		font-weight : 500;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
