  box-shadow: none;
  cursor: pointer;
}
.noUi-handle:after {
  display: none;
}
.noUi-handle:before {
  background-color: #FFF;
  border-radius: 100%;
  height: 8px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 8px;
}

.noUi-horizontal .noUi-handle {
  height: 24px;
  right: -17px;
  top: -10px;
  width: 24px;
}

.noUi-touch-area {
  height: 100%;
  width: 100%;
}

