.cta {
	padding : 8px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 12px 0;
	}
}


.cta__wrapper {
	background    : $bg-blue;
	border-radius : 30px;
	min-height    : 490px;
	overflow      : hidden;
	padding       : 40px 40px;
	position      : relative;

	@media screen and (min-width : $break-table) {
		min-height : 748px;
		padding    : 64px 64px;
	}

	@media screen and (min-width : $break-desktop) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
		min-height      : auto;
		padding         : 104px 64px;
	}

	.b-btn--main {
		min-width : 248px;
		padding   : 12px 32px;

		span {
			font-size   : 16px;
			font-weight : 500;
			line-height : 24px;
		}

		@media screen and (min-width : $break-table) {
			min-width : 206px;
			padding   : 20px 32px;

			span {
				font-size   : 24px;
				line-height : 32px;
			}

		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.cta__text {
	color         : $white;
	font-size     : 24px;
	font-weight   : 600;
	line-height   : 32px;
	margin-bottom : 24px;
	max-width     : 246px;

	@media screen and (min-width : $break-table) {
		font-size     : 48px;
		line-height   : 56px;
		margin-bottom : 32px;
		max-width     : 544px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;

	}
}

.cta__image {
	bottom   : 0;
	position : absolute;
	right    : 19px;
	width    : 280px;

	@media screen and (min-width : $break-table) {
		right : 0;
		width : 569px;
	}

	@media screen and (min-width : $break-desktop) {
		right : 320px;
		width : 404px;
	}

}

