.scheme {
	padding : 8px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 12px 0;
	}
}

.scheme__wrapper {
	background-color : $light-blue;
	border-radius    : 30px;
	padding          : 37px 40px;

	@media screen and (min-width : $break-table) {
		padding : 64px 64px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 64px 64px 128px 64px;
	}
}

.scheme__head {
	margin-bottom : 34px;

	@media screen and (min-width : $break-table) {
		align-items     : flex-start;
		display         : flex;
		justify-content : space-between;
		margin-bottom   : 64px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 96px;
	}
}

.scheme__title {
	color         : #262526;
	margin-bottom : 27px;


	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		max-width     : 220px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : none;
	}

}

.scheme__text {
	color       : #0B0B0B;
	font-size   : 14px;
	font-weight : 500;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
		max-width   : 328px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 473px;
	}
}

.scheme__grid {
	display               : grid;
	grid-gap              : 8px;
	grid-template-columns : repeat(4, 1fr);
	position              : relative;

	@media screen and (min-width : $break-table) {
		margin    : 0 auto;
		max-width : 578px;
	}

	@media screen and (min-width : $break-desktop) {
		background-image      : url("../images/home/scheme/scheme-bg.png");
		background-position   : center center;
		background-repeat     : no-repeat;
		background-size       : 915px 378px;
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
		margin                : -56px 0;
		max-width             : none;
		padding               : 56px 0;
	}


}

.scheme__item {
	border-radius : 30px;
	grid-column   : 2 span;

	&:first-child {
		position : relative;

		&:after {
			background-image  : url("../images/home/scheme/arrows-top.svg");
			background-repeat : no-repeat;
			background-size   : cover;
			content           : "";
			height            : 38px;
			position          : absolute;
			right             : -30px;
			top               : 50%;
			transform         : translateY(-50%);
			width             : 59px;
			z-index           : 1;


			@media screen and (min-width : $break-table) {
				height : 52px;
				right  : -40px;
				width  : 78px;
			}

			@media screen and (min-width : $break-desktop) {
				display : none;
			}
		}
	}

	&:last-child {
		position : relative;

		&:after {
			background-image  : url("../images/home/scheme/arrows-bottom.svg");
			background-repeat : no-repeat;
			background-size   : cover;
			content           : "";
			height            : 38px;
			left              : -35px;
			position          : absolute;
			top               : 50%;
			transform         : translateY(-50%);
			width             : 59px;
			z-index           : 1;


			@media screen and (min-width : $break-table) {
				height : 52px;
				left   : -45px;
				width  : 81px;
			}

			@media screen and (min-width : $break-desktop) {
				display : none;
			}
		}
	}


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 3 span;
	}
}

.scheme__img {
	display         : flex;
	flex-direction  : column;
	justify-content : flex-end;

	img {
		object-fit : cover;
		width      : 100%;
	}
}
