.calculator {
	padding : 8px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 12px 0;
	}
}

.calculator__grid {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}
}

.calculator__main {
	background-color : $light-blue;
	border-radius    : 30px;
	display          : flex;
	flex-direction   : column;
	grid-column      : 4 span;
	padding          : 40px 40px;

	@media screen and (min-width : $break-table) {
		padding : 64px 64px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 8 span;

	}

	.b-btn--blue {
		border        : none;
		border-radius : 24px;
		font-size     : 24px;
		font-weight   : 500;
		line-height   : 24px;
		margin-top    : 32px;
		min-width     : 248px;
		padding       : 12px 16px;

		@media screen and (min-width : $break-table) {
			font-size   : 24px;
			line-height : 32px;
			min-width   : 175px;
			padding     : 20px 32px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-left : auto;
		}
	}
}

.calculator__title {
	margin-bottom : 40px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 64px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.calculator__top {
	margin-bottom : 40px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.calculator__label {
	display        : flex;
	flex-direction : column;
	position       : relative;

	span {
		color          : #0B0B0B;
		font-size      : 16px;
		font-weight    : 500;
		line-height    : 24px;
		padding-bottom : 16px;
	}

	&:after {
		color       : #0B0B0B;
		content     : "$";
		font-size   : 20px;
		font-weight : 700;
		left        : 20px;
		line-height : 28px;
		position    : absolute;
		top         : 67px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 24px;
			left        : 20px;
			line-height : 32px;
			top         : 59px;
		}
	}

	input {
		background-color : $white;
		border           : none;
		border-radius    : 8px 8px 0px 0px;
		color            : #0B0B0B;
		font-size        : 20px;
		font-weight      : 700;
		line-height      : 28px;
		padding          : 28px 32px 24px 40px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 24px;
			line-height : 32px;
			padding     : 20px 32px 20px 40px;
		}

		&::placeholder {
			color       : #0B0B0B;
			font-size   : 20px;
			font-weight : 700;
			line-height : 28px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				font-size   : 24px;
				font-weight : 700;
				line-height : 32px;
			}
		}
	}
}

.calculator__wrap {
	display         : flex;
	justify-content : space-between;
	margin-top      : 16px;

	span {
		color       : #0B0B0B;
		font-size   : 16px;
		font-weight : 500;
		line-height : 24px;
		opacity     : 0.8;
	}
}

.calculator__bottom {
	background-color : $white;
	border-radius    : 8px;
	padding          : 32px 32px;

	@media screen and (min-width : $break-table) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.calculator__text {
	color         : #0B0B0B;
	font-size     : 16px;
	font-weight   : 600;
	line-height   : 24px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		font-size     : 24px;
		line-height   : 32px;
		margin-bottom : 0;
		max-width     : 205px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : none;
	}
}

.calculator__price {
	color       : #0D50C3;
	font-size   : 32px;
	font-weight : 600;
	line-height : 40px;

	@media screen and (min-width : $break-table) {
		font-size   : 48px;
		line-height : 56px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.calculator__img {
	background    : $bg-blue;
	border-radius : 30px;
	display       : none;
	grid-column   : 4 span;
	overflow      : hidden;
	padding-top   : 61px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display : block;
	}
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	/* display: none; <- Crashes Chrome on hover */
	-webkit-appearance : none;
	margin             : 0; /* <-- Apparently some margin are still there even though it's hidden */
}

input[type=number] {
	-moz-appearance : textfield; /* Firefox */
}
