.why {
	padding : 8px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 12px 0;
	}
}


.why__grid {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 24px;
	}
}

.why__item {
	border-radius : 30px;
	overflow      : hidden;
	padding       : 32px 32px;
	position      : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

	&:nth-child(1) {
		background  : $bg-blue;
		grid-column : 4 span;
		min-height  : 296px;
		padding     : 40px 40px 0 40px;

		@media screen and (min-width : $break-table) {
			grid-column : 12 span;
			padding     : 64px 64px;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 1/5;
			grid-row    : 1/8;
			min-height  : 852px;
			padding     : 64px 64px 0 64px;
		}

		.why__image {
			bottom   : 0;
			position : absolute;
			right    : 15px;
			width    : 173px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				right : 40px;
				width : 262px;
			}

			@media screen and (min-width : $break-desktop) {
				left      : 50%;
				right     : 15px;
				transform : translateX(-50%);
				width     : 100%;
			}

			img {
				object-fit : cover;
				width      : 100%;
			}
		}
	}

	&:nth-child(2) {
		background-color : $light-blue;
		grid-column      : 4 span;
		min-height       : 411px;

		@media screen and (min-width : $break-table) {
			grid-column : 1/7;
			grid-row    : 2/4;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 5/9;
			grid-row    : 1/5;
		}

		.why__about {
			color         : #0B0B0B;
			margin-bottom : 24px;
			max-width     : 263px;
			opacity       : 0.8;
		}

		.why__text {
			opacity : 0.8;
		}

		.why__image {
			bottom   : 0;
			position : absolute;
			right    : 39px;
			width    : 169px;

			@media screen and (min-width : $break-table) {
				width : 188px;
			}

			@media screen and (min-width : $break-desktop) {
				right : 17px;
				width : 228px;
			}

			img {
				height     : 100%;
				object-fit : cover;
				width      : 100%;
			}
		}
	}

	&:nth-child(3) {
		background-color : $light-blue;
		grid-column      : 4 span;
		min-height       : 210px;

		@media screen and (min-width : $break-table) {
			grid-column : 1/7;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 5/9;
			grid-row    : 5/6;
		}

		.why__about {
			color         : #0B0B0B;
			margin-bottom : 8px;
			max-width     : 150px;
			opacity       : 0.8;
		}

		.why__text {
			color     : #0B0B0B;
			max-width : 157px;
			opacity   : 0.8;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				max-width : 192px;
			}
		}

		.why__image {
			position : absolute;
			right    : 29px;
			top      : 28px;
			width    : 101px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				width : auto;
			}
		}
	}

	&:nth-child(4) {
		background  : $bg-blue;
		grid-column : 4 span;
		min-height  : 210px;

		@media screen and (min-width : $break-table) {
			grid-column : 7/13;
			grid-row    : 2/2;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 9/13;
			grid-row    : 1/3;
		}

		.why__about {
			color         : $white;
			margin-bottom : 8px;
			max-width     : 205px;
		}

		.why__text {
			color : $white;
		}

		.why__image {
			bottom   : 0;
			position : absolute;
			right    : 0;
			width    : 161px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				width : auto;
			}
		}
	}

	&:nth-child(5) {
		background-color : $light-blue;
		grid-column      : 4 span;
		min-height       : 210px;

		@media screen and (min-width : $break-table) {
			grid-column : 7/13;
			grid-row    : 3/3;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 9/13;
			grid-row    : 3/5;
		}

		.why__about {
			color         : #0B0B0B;
			margin-bottom : 8px;
			max-width     : 205px;
		}

		.why__text {
			color     : #0B0B0B;
			max-width : 113px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				max-width : none;
			}
		}

		.why__image {
			bottom   : 0;
			position : absolute;
			right    : 23px;
		}
	}

	&:nth-child(6) {
		background  : $bg-blue;
		grid-column : 4 span;
		min-height  : 210px;

		@media screen and (min-width : $break-table) {
			grid-column : 7/13;
			grid-row    : 4/4;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 9/13;
			grid-row    : 5/6;
		}

		.why__about {
			color         : $white;
			margin-bottom : 8px;
			max-width     : 158px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				max-width : 216px;
			}
		}

		.why__text {
			color     : $white;
			max-width : 100px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				max-width : none;
			}
		}

		.why__image {
			bottom   : 0;
			position : absolute;
			right    : 0;
			width    : 162px;


			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				width : auto;
			}
		}
	}

	&:nth-child(7) {
		background  : $bg-blue;
		grid-column : 4 span;
		min-height  : 495px;

		@media screen and (min-width : $break-table) {
			background-image  : url("../images/home/why/product-desktop.png");
			background-repeat : no-repeat;
			background-size   : cover;
			grid-column       : 12 span;
			min-height        : 175px;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 5/13;
			grid-row    : 6/8;
			min-height  : 210px;
		}

		.why__about {
			color     : $white;
			max-width : 154px;

			@media screen and (min-width : $break-table) {
				max-width : 186px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		.why__link {
			align-items : center;
			color       : $white;
			display     : inline-flex;
			font-size   : 16px;
			font-weight : 600;
			line-height : 24px;

			&:nth-of-type(1) {
				position : absolute;
				right    : 32px;
				top      : 168px;

				@media screen and (min-width : $break-table) {
					bottom : 32px;
					left   : 32px;
					right  : unset;
					top    : unset;
				}
			}

			&:nth-of-type(2) {
				bottom   : 199px;
				left     : 33px;
				position : absolute;

				@media screen and (min-width : $break-table) {
					bottom : 32px;
					left   : unset;
					right  : 251px;
				}
			}

			&:hover {
				&:after {
					transform : rotate(45deg);
				}
			}

			&:after {
				background-image    : url("../images/icon/arrow-white.svg");
				background-position : center center;
				background-repeat   : no-repeat;
				background-size     : 24px 24px;
				border-radius       : 100%;
				content             : "";
				display             : inline-block;
				height              : 24px;
				margin-left         : 8px;
				transform           : rotate(0deg);
				transition          : transform .3s ease;
				width               : 24px;
			}

		}
	}
}

.why__text {
	font-size   : 16px;
	font-weight : 500;
	line-height : 24px;
}

.why__about {
	font-size   : 20px;
	font-style  : normal;
	font-weight : 500;
	line-height : 28px;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.why__title {
	color       : $white;
	font-size   : 32px;
	font-weight : 600;
	line-height : 40px;

	@media screen and (min-width : $break-table) {
		font-size   : 48px;
		line-height : 56px;
		max-width   : 210px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : none;
	}
}

.why__phone {
	bottom    : 0;
	left      : 50%;
	position  : absolute;
	transform : translateX(-50%);
	width     : 266px;

	@media screen and (min-width : $break-table) {
		display : none;
	}
}

.why__astra {
	left     : 32px;
	position : absolute;
	top      : 128px;
	width    : 110px;

	@media screen and (min-width : $break-table) {
		display : none;
	}
}
