// -----------------------------------------------
//  Form config
// -----------------------------------------------

$input-background          : transparent;
$input-border              : 1px solid #C1C1C1;
$input-padding             : 19px 28px;
$input-radius              : 5px;

$input-font-size           : 16px;
$input-font-weight         : 400;
$input-line-height         : 1.25;
$input-color               : #4B4B4E;
$input-transition          : border-color .3s ease;

// Placeholder
$input-placeholder         : #4B4B4E;

// Focus
$input-border-focus        : #4B4B4E;

// Error
$input-border-error        : $red;

// Disabled
$input-border-disabled     : #BCBCBC;
$input-background-disabled : #F9F9F9;
$input-color-disabled      : #939393;

// -----------------------------------------------
//  Form style
// -----------------------------------------------
input[type="text"],
input[type="email"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="password"],
select,
textarea {
	background-color   : $input-background;
	border             : $input-border;
	border-radius      : $input-radius;
	outline            : 0;
	height             : auto;
	line-height        : $input-line-height;
	color              : $input-color;
	max-width          : 100%;
	font-weight        : $input-font-weight;
	transition         : $input-transition;
	padding            : $input-padding;
	font-size          : $input-font-size;
	-webkit-appearance : none;
	box-sizing         : border-box;
	@include placeholder() {
		font-weight : $input-font-weight;
		color       : $input-placeholder;
	}

	&:focus {
		border-color : $input-border-focus;
	}

	&.error {
		border-color : $input-border-error;
		color        : $red;
	}

	&.disabled,
	&[disabled] {
		cursor           : default;
		background-color : $input-background-disabled;
		border-color     : $input-border-disabled;
		color            : $input-color-disabled;
	}

	&[readonly] {
		cursor           : default;
		background-color : $input-background-disabled;
		border-color     : $input-border-disabled;
		color            : $input-color-disabled;
	}
}

select {
	padding-right       : 35px;
	background-image    : url('../images/select.svg');
	background-position : 100% 50%;
	background-repeat   : no-repeat;
}

input[type="radio"],
input[type="checkbox"] {
	display        : inline-block;
	vertical-align : middle;
}

textarea {
	height   : auto;
	overflow : auto;
}

.error-message {
	color      : $red;
	margin-top : 5px;
	order      : 1;
}

label.checkbox-style,
.checkbox-style {
	font-size                   : 13px;
	font-weight                 : 400;
	line-height                 : 1.3;
	position                    : relative;
	display                     : block;
	overflow                    : hidden;
	margin                      : 0;
	cursor                      : pointer;
	user-select                 : none;
	-webkit-tap-highlight-color : transparent;
	outline                     : 0;
	color                       : #A7A8B1;

	> span {
		position       : relative;
		display        : inline-block;
		vertical-align : top;
		color          : #A7A8B1;
		padding        : 2px 0 2px 28px;

		&:before {
			content       : '';
			position      : absolute;
			z-index       : 1;
			top           : 0;
			left          : 0;
			box-sizing    : border-box;
			width         : 20px;
			height        : 20px;
			border        : 1px solid #C1C1C1;
			border-radius : 3px;
			transition    : border-color .3s ease, background-color .3s ease;
		}

		&:after {
			content       : '';
			position      : absolute;
			z-index       : 5;
			top           : 3px;
			left          : 3px;
			opacity       : 0;
			transition    : opacity .3s ease;
			width         : 14px;
			height        : 14px;
			background    : #000;
			border-radius : 3px;
		}
	}

	input[type="checkbox"] {
		position                    : absolute;
		top                         : 0;
		left                        : -30px;
		width                       : 1px;
		height                      : 1px;
		outline                     : 0;
		-webkit-tap-highlight-color : transparent;

		&:checked {
			~ span {
				&:before {
				}

				&:after {
					opacity : 1;
				}
			}
		}

		&.disabled,
		&[disabled] {
			~ span {
				&:after {
					background : #E3E3E3;
				}
			}
		}
	}
}