* {
	-webkit-tap-highlight-color : rgba(0, 0, 0, 0);
}

html {
	box-sizing : border-box;
	height     : 100%;
}

*,
*::before,
*::after {
	box-sizing : inherit;
}

.page-wrapper {
	display        : flex;
	flex-direction : column;
	min-height     : 100%;
	overflow       : hidden;
	position       : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.main {
	flex : 1 0 auto;
}

.main--offset {
	border        : 1px solid #CCD9E5;
	border-radius : 8px;
}

.container {
	margin    : 0 auto;
	max-width : 1352px;
	padding   : 0 16px;
	width     : 100%;

	@media screen and (min-width : $break-table) {
		max-width : 1368px;
		padding   : 0 24px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.section-title {


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.section-head {

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.box-item {
	border-radius : 30px;
	padding       : 40px 40px;

	@media screen and (min-width : $break-desktop) {
		padding : 64px 64px;
	}

	&--background {
		background-color : $light-blue;
	}

	&--nopadding {
		padding : 0;
	}
}

.section-link {
	align-items : center;
	color       : #0E51C4;
	display     : inline-flex;
	font-size   : 16px;
	font-style  : normal;
	font-weight : 600;
	line-height : 24px;
	opacity     : 0.8;
	transition  : opacity .3s ease;

	&:hover {
		opacity : 1;

		&:after {
			transform : rotate(45deg);
		}
	}

	&:after {
		background-color    : $blue;
		background-image    : url("../images/icon/arrow-right.svg");
		background-position : center center;
		background-repeat   : no-repeat;
		background-size     : 14px 14px;
		border-radius       : 100%;
		content             : "";
		display             : inline-block;
		height              : 24px;
		margin-left         : 8px;
		transform           : rotate(0deg);
		transition          : transform .3s ease;
		width               : 24px;
	}
}

.lang-panel {
	background-color : $white;
	border-radius    : 64px;
	margin-left      : 8px;
	padding          : 8px 12px;
	position         : relative;
	transition       : background-color .3s ease;
	@media screen and (min-width : $break-table) {
		margin-left : 10px;
	}

	@media screen and (min-width : 1400px) {
		margin-left : 24px;
	}

	&:hover {
		background-color : $blue;

		.lang-panel__link {
			span {
				color : $white;
			}

			svg {
				path {
					stroke : $white;
				}
			}
		}
	}
}

.lang-panel.is-open {
	background-color : $blue;


	.lang-panel__dropdown {
		display   : flex;
		opacity   : 1;
		transform : translateX(-50%) scaleY(1);
	}

	.lang-panel__link {
		span {
			color : $white;
		}

		svg {
			path {
				stroke : $white;
			}
		}
	}


}

.lang-panel__link {
	align-items      : center;
	background-color : transparent;
	border           : none;
	border-radius    : 10px;
	cursor           : pointer;
	display          : flex;
	flex-flow        : row nowrap;
	justify-content  : flex-start;
	padding          : 0;
	position         : relative;
	width            : 100%;

	@media screen and (min-width : $break-table) {

	}


	svg {
		margin-left : 10px;

		path {
			stroke     : $blue;
			transition : stroke .3s ease;
		}
	}

	span {
		color       : $blue;
		font-size   : 14px;
		font-style  : normal;
		font-weight : 500;
		line-height : 20px;
		text-align  : center;
		transition  : color .3s ease;
	}
}

.lang-panel__dropdown {
	background-color : #F2F8FF;
	border-radius    : 30px;
	display          : flex;
	flex-direction   : column;
	height           : auto;
	left             : 50%;
	opacity          : 0;
	overflow-y       : unset;
	padding          : 24px 24px;
	position         : absolute;
	top              : 48px;
	transform        : translateX(-50%) scaleY(0);
	transform-origin : 0 0;
	transition       : transform .3s ease, visibility .3s ease, opacity .3s ease-in;
	visibility       : visible;
	width            : 104px;
	z-index          : 6;
	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		top : 45px;
	}

	ul {
		width : 100%;
		@media screen and (min-width : $break-table) {
			margin-bottom : 0;
			padding       : 0;
		}

		li {
			display       : block;
			margin-bottom : 24px;
			position      : relative;

			&:last-child {
				margin-bottom : 0;
			}

			@media screen and (min-width : $break-table) {

			}

			button {
				background-color : transparent;
				border           : none;
				color            : $black;
				cursor           : pointer;
				display          : block;
				font-size        : 14px;
				font-style       : normal;
				font-weight      : 500;
				line-height      : 20px;
				padding          : 0;
				transition       : color .3s ease;
				width            : 100%;


				&:hover {
					color : $blue;
				}

				&.active {
					color : $blue;
				}
			}


			@media screen and (min-width : $break-table) {

			}

		}
	}
}

.section-title {
	font-size   : 32px;
	font-style  : normal;
	font-weight : 600;
	line-height : 40px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 48px;
		font-weight : 600;
		line-height : 56px;
	}
}

.range {
	width : 99%;
}

.noUi-target {
	background    : transparent;
	border        : none;
	border-radius : 4px;
	box-shadow    : none;
}

.range.noUi-horizontal {
	height : 4px !important;
}

.noUi-connect {
	background : $blue;
}

.noUi-handle {
	background    : $blue;
	border        : none;
	border-radius : 100%;
	box-shadow    : none;
	cursor        : pointer;

	&:after {
		display : none;
	}

	&:before {
		background-color : $white;
		border-radius    : 100%;
		height           : 8px;
		left             : 50%;
		top              : 50%;
		transform        : translate(-50%, -50%);
		width            : 8px;
	}
}

.noUi-horizontal .noUi-handle {
	height : 24px;
	right  : -17px;
	top    : -10px;
	width  : 24px;
}

.noUi-touch-area {
	height : 100%;
	width  : 100%;


}
