@font-face {
	font-family  : 'TTFirsNeue';
	src          : url('../fonts/TTFirsNeue-Regular.woff2') format('woff2'),
	url('../fonts/TTFirsNeue-Regular.woff') format('woff'),
	url('../fonts/TTFirsNeue-Regular.ttf') format('truetype');
	font-weight  : 400;
	font-style   : normal;
	font-display : swap;
}

