
.overlay {
	background : rgba(0, 0, 0, 0.85);
	bottom     : 0;
	left       : 0;
	opacity    : 0;
	position   : fixed;
	right      : 0;
	//z-index: -1;
	top        : 0;
	transition : opacity 0.3s ease, visibility .3s ease;
	visibility : hidden;
	z-index    : 101;

	&.active {
		opacity    : 1;
		visibility : visible;
	}
}

.popup {
	display    : none;
	left       : 50%;
	max-width  : 348px;
	padding    : 0 15px;
	//z-index: -3;
	position   : fixed;
	top        : 50%;
	transform  : translate(-50%, -50%);
	transition : opacity 0.5s ease;
	//opacity: 0;
	width      : 100%;
	z-index    : 103;

	@media screen and (min-width : $break-table) {
		max-width : 720px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&.active {
		display    : block;
		//opacity: 1;
		transition : opacity 0.5s ease;
		//transform: translate(-50%, -45%);
		z-index    : 103;
	}

	&__wrap {
		align-items      : center;
		background-color : $blue;
		border-radius    : 30px;
		display          : flex;
		min-height       : 220px;
		overflow         : hidden;
		padding          : 16px;
		position         : relative;
	}

	&__close {
		background-color : transparent;
		border           : none;
		height           : 32px;
		padding          : 0;
		position         : absolute;

		right            : 23px;
		top              : 23px;
		width            : 32px;
		z-index          : 999;

		svg {
			height : 32px;
			width  : 32px;
		}

		@media screen and (min-width : $break-table) {
			height : 64px;
			right  : 16px;
			top    : 16px;
			width  : 64px;

			svg {
				height : 64px;
				width  : 64px;
			}
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.popup--connection {
	overflow : hidden;
	padding  : 0 15px;


	@media screen and (min-width : $break-table) {
		max-width : 720px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 1230px;
		padding   : 0;
	}

	.popup__wrap {
		padding : 16px 16px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
		}
	}
}

.connection {
	display    : flex;
	margin-top : 223px;
	position   : relative;
	width      : 100%;

	&:before {
		background-image  : url("../images/icon/modal-mobile.png");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		height            : 202px;
		left              : 50%;
		position          : absolute;
		top               : -199px;
		transform         : translateX(-50%);
		width             : 242px;
		z-index           : 1;

		@media screen and (min-width : $break-table) {
			height : 348px;
			top    : -342px;
			width  : 407px;
		}

		@media screen and (min-width : $break-desktop) {
			background-image : url("../images/icon/modal-desktop.png");
			bottom           : -16px;
			height           : 699px;
			left             : unset;
			right            : -16px;
			top              : unset;
			transform        : unset;
			width            : 560px;
		}
	}

	@media screen and (min-width : $break-table) {
		margin-top : 340px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 0;
	}
}

.connection__left {
	background-color : $white;
	border-radius    : 30px;
	min-height       : 100%;
	padding          : 16px 16px;
	position         : relative;
	width            : 100%;
	z-index          : 2;

	@media screen and (min-width : $break-table) {
		padding : 64px 64px;
	}

	@media screen and (min-width : $break-desktop) {
		min-width : 648px;
		width     : auto;
	}
}


.connection__title {
	color         : #262526;
	font-size     : 24px;
	font-weight   : 600;
	line-height   : 32px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		font-size     : 48px;
		line-height   : 56px;
		margin-bottom : 24px;
		max-width     : 496px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.connection__text {
	color         : #0B0B0B;
	font-size     : 14px;
	font-weight   : 500;
	line-height   : 20px;
	margin-bottom : 16px;
	max-width     : 343px;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 64px;
		max-width     : 343px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}


.connection__item {
	&:not(:last-child) {
		margin-bottom : 17px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 0;
		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 48px;
		}
	}
}

.connection__name {
	color       : #0B0B0B;
	font-size   : 14px;
	font-weight : 500;
	line-height : 20px;
	opacity     : 0.8;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 9px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.connection__link {
	color       : #262526;
	font-size   : 20px;
	font-weight : 800;
	line-height : 28px;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		font-weight : 800;
		line-height : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
